<template>
    <header class="d-flex flex-column"  v-on:mouseover="search_field_focus">
        <div id="header" class="d-flex align-items-center justify-content-center">
            <span>MY CONTACTS</span>            
            <i class="fas fa-search" v-on:click="open_search_field" id="search-btn"></i>
        </div>
        <div :class="[searchIndex === 1 ? 'search' : 'hide']" class="align-items-center">
            <input v-on:input="search_contacts" type="search" id="search-field" placeholder="search by name" v-model="search_query">
            <i class="fas fa-times" v-on:click="close_search_field"></i>
        </div>
        
    </header>
</template>


<script>

export default {
    name: "Header",
    props: ["contacts", "searchIndex"],
    data: function(){
        return{
            search_query: ""
        }
    },
    methods: {
        open_search_field: function(){
            this.$emit("open-search-field");
        },
        close_search_field: function(){
            this.$emit("close-search-field");
            this.contacts.items = this.contacts.loadFromLocalStorage();
            this.search_query = '';                
        },
        search_contacts: function(){ 
            this.contacts.items = this.contacts.loadFromLocalStorage();
            let result = [];     
            for(let i=0; i<this.contacts.items.length; i++){
                if(this.contacts.items[i].name.includes(this.search_query)){
                    result.push(this.contacts.items[i]);
                }
            }
            console.log(result);
            this.contacts.items = result;
        },
        search_field_focus: function(){
            let search_field = document.getElementById("search-field");
            search_field.focus();
        }
    }
};


</script>


<style>
header{
    flex-shrink: 0;
    overflow: hidden;
    border-top-left-radius : 10px;
    border-top-right-radius: 10px;
    color: grey;
    background: #f9f9f9;
    font-size: 24px;
}

#header{
    height : 89px;
    position: relative;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: blue transparent;

}
#search-btn {    
    position: absolute;
    margin-top: 5px;
    right: 8px;
    cursor: pointer;    
    padding: 6px;
    color       : rgb(190, 190, 190);
    border-radius: 4px;
    font-size: 23px;
}

#search-btn:hover {
    background-color: rgb(240, 240, 240);
}

.search {
    display: flex;
    padding: 0 12px 12px 16px;
    background-color: #f9f9f9;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: blue transparent;
}

.hide {
    display: none;
}

#search-field {
    flex-grow: 1;
    border: none;
    background-color: rgb(240, 240, 240);
    border-radius: 4px;
    font-size: 14px;
}

#search-field:focus,
#search-field:active,
#search-field:hover {
    border : none;
    outline: none;
}

.fa-times{
    font-size   : 18px;
    color       : rgb(190, 190, 190);
    border-radius: 4px;
    padding     : 2px 7px;
    margin-left : 18px;
    cursor      : pointer;
}
.fa-times:hover{
    background-color: rgb(240, 240, 240);
}


@media (min-width: 414px){
    #header{
        height: 74px;
        font-size: 26px;
    }
}

::-webkit-scrollbar{
    width: 1px;
}


</style>