<template>
    <div class="d-flex content-element">
        <div class="col-4 col-sm-3">{{content.date}}</div>
        <div class="col-8 col-sm-9 px-0 d-flex justify-content-between">
            <span>{{content.text}}</span>
            <i class="fas fa-minus" aria-hidden="true" v-on:click="delete_content"></i>  
        </div>  
    </div>
</template>

<script>

export default {
    name:"ItemContent",
    props: ["content"],
    methods: {
        delete_content: function(){
            this.$emit("delete-content");
        }
    }
}

</script>

<style>

.fa-minus{
    padding     : 6px;
    cursor      : pointer;
    color       : rgb(160, 160, 160);
}

span{
    overflow: hidden;
    overflow-wrap: break-word;
}
</style>