<template>
    <div class="contacts-item d-flex flex-column" >
        <div class="d-flex align-items-center">
            <div class="contacts-first-letter d-flex" :style="{'background-color': item.color}" v-on:mouseenter="open_reminder" v-on:click="open_reminder">
                <span>{{item.name.split(' ').map(word => word.charAt(0).toUpperCase()).join('')}}</span>
            </div>
            <div class="contacts-item-content d-flex flex-column">
                <span id="name" class="data">{{item.name.split(' ').map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()).join(' ')}}</span>
                <span id="address">{{item.address.split(', ').map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()).join(', ')}}</span>
                <span id="number" class="data">{{item.number}}</span>
            </div>
            <i class="fa fa-trash" :class="searchIndex == 1 ? 'hide' : ''" title="delete" v-on:click.stop="onDelete" aria-hidden="true"></i>
            <i class="fa fa-trash" :class="searchIndex == -1 ? 'hide' : 'disable'"  title="delete" aria-hidden="true"></i>
        </div>
        <div class="flex-column reminder" :class="open_index == -1 ? 'hide' : ''" v-on:mouseenter="$event.target.children[2].children[1].children[0].focus()">
            <div class="d-flex justify-content-between">
                <span>REMINDER</span>
                <i class="fas fa-times" aria-hidden="true" v-on:click="submit_content($event.target.parentElement.nextSibling.nextSibling.children[0].innerHTML)"></i>
            </div>
            <div class="history" v-on:mouseenter="$event.target.scrollTop=$event.target.scrollHeight-$event.target.clientHeight">
                <item-content v-for="(content,index) in item.content" v-bind:content="content" :key="index" :item="item" v-on:delete-content="delete_content(index)"></item-content>
            </div>
            <div class="d-flex">
                <div class="col-4 col-sm-3" id="date">{{(new Date()).toLocaleString("ro-RO", { year: 'numeric', month: 'numeric', day: 'numeric' })}}</div>
                <div class="col-8 col-sm-9 px-0">
                    <textarea v-model="text" class="form-control"></textarea>
                </div>
            </div>
        </div>
    </div>
</template>



<script>

    import ContentItem from '../model/ContentItem';
    import ItemContent from './ItemContent';

    export default {
        name: "Item",
        props: ["item", "searchIndex", "contacts"],
        components:{
            'item-content': ItemContent
        },
        data: function(){
            return {
                text : '',
                open_index: -1
            }
        },
        methods: {
            onDelete: function(){
                this.$emit("item-delete");
            },
            submit_content: function(date){

                if(this.text){
                    let contentItem = new ContentItem(date,this.text);
                    this.item.content.push(contentItem);
                    this.contacts.saveToLocalStorage();

                    this.text = '';
                }
                
                this.open_index = -1;
            }, 
            open_reminder: function(){
                this.open_index = 1;
            },
            delete_content: function(index){
                this.item.content.splice(index, 1);
                this.contacts.saveToLocalStorage();
            }
        }
    };

</script>


<style scoped>

.contacts-item {
    padding      : 8px 4px;
    margin-left  : 12px;
    margin-right : 12px;
    border-bottom: 1px solid #e8e8e8;
    color        : rgb(110, 110, 110);
}
.contacts-item:hover{
    color: rgb(35, 35, 35);
}

.contacts-first-letter {
    width         : 50px;
    height        : 50px;
    background    : #ecb2c3;
    border-radius : 50%;
    color         : white;
    cursor        : pointer;
}
.contacts-first-letter span{
    margin: auto;
    padding-bottom: 3px;
}

.contacts-item-content {
    flex-grow: 1;
    padding: 10px;
    padding-right: 24px; 
    font-weight: 600;

}

#name {
    font-size: 15px;
}
#number{
    margin-top: -5px;
}
#address {
    font-size: 15px;
    font-weight: normal;
    margin-top: -5px;
}

.fa-trash {
    display     : inline-block;
    width       : 19px;
    height      : 22px;
    color       : rgb(220, 220, 220);
    flex-shrink : 0;
    font-size   : 22px;
    cursor      : pointer;
}

.fa-trash:hover {
    color: rgb(180, 180, 180);
}
.disable:hover{
    color: rgb(220, 220, 220);
    cursor: auto;
}

@media (min-width: 414px){
    #number{
        align-self: flex-end;
    }
}


.reminder{
    display: flex;
    border: 1px solid grey;
    padding: 6px;
    border-radius: 4px;
}
.reminder>div:first-child{
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
}
.reminder span{
    font-weight: 600;
}
.reminder .fa-times{
    color       : rgb(160, 160, 160);
}
.reminder>div:nth-child(2){
    max-height: 120px;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
    margin-bottom: 10px;
}
.reminder>div:nth-child(2)::-webkit-scrollbar{
    width: 1px;
}

textarea{
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
}
.hide{
    display: none;
}

::-webkit-scrollbar{
    width: 1px;
}


</style>