<template>
    <div id="app-body" >
        <span class="no-items-message">No Contacts</span>
        <item         
            v-for="(item,index) in contacts.items"
            v-bind:key="item.id"
            v-bind:item = "item"
            v-on:item-delete="onDelete(index)" :searchIndex="searchIndex" :contacts="contacts"
        ></item>
    </div>
</template>

<script>

    import Item from "./Item";

    export default {
        name: "Body",
        props:["contacts", "searchIndex"],
        components: {
            item : Item
        },
        methods: {
            onDelete:function(index){
                this.contacts.delete(index);
            }
        }
    };

</script>


<style scoped>

    #app-body {
        flex-grow: 1;
        overflow-y : scroll;
        display  : flex;
        flex-direction: column;
        scrollbar-width: thin;
        scrollbar-color: #e2e2e2 transparent;
    }

    .no-items-message {
        display: none; 
        margin: auto;
    }

    .no-items-message:only-child {
        display: block;
    }

    ::-webkit-scrollbar{
        width: 1px;
    }
</style>