<template>
    <footer >
        <div class="d-flex align-items-center">
            <div class="d-flex flex-column flex-grow-1 footer-container">
                <div class="d-flex content">
                    <div class="d-flex flex-grow-1 name"> 
                        <i class="fa fa-user icon" :class="warning_user === 1 ? 'warning' : ''"></i> 
                        <input v-on:mouseover="$event.target.focus()" @keyup.enter="$event.target.parentElement.nextSibling.children[1].focus()" class="input-field" id="name" type="text" placeholder="contact's name..." v-model="name"> 
                    </div> 
                    <div class="d-flex flex-grow-1 number"> 
                        <i class="fas fa-phone-alt icon" :class="{'warning': warning_phone == 1}"></i>
                        <input @keyup.enter="$event.target.parentElement.parentElement.nextSibling.children[1].focus()" class="input-field" id="number" type="text" placeholder="contact's number..." v-model="number"> 
                    </div>
                </div>

                <div class="d-flex flex-grow-1"> 
                    <i class="fas fa-home icon" :class="{'warning': warning_address == 1}" ></i>
                    <input v-on:keyup.enter="enter_data" class="input-field" id="address" type="text" placeholder="contact's address..." v-model="address"> 
                </div>
            </div>

            <span id="add-btn" v-on:click="add_item(name, number, address)">Add</span>
        </div>
    </footer>
</template>

<script>
    import ContactItem from '../model/ContactItem';

    export default {
        name: "Footer",
        props: ["contacts"],
        data: function(){
            return{
                name: "",
                number: "",
                address: "",
                warning_user: -1,
                warning_phone: -1,
                warning_address: -1
            };
        },
        methods:{
            add_item: function(name, number, address){

                if(name && number && address){
                    let item = new ContactItem(name, number, address);
                    this.contacts.add(item);
                    this.warning_user = -1;
                    this.warning_phone = -1;
                    this.warning_address = -1;
                    this.name = '';
                    this.number = '';
                    this.address = '';
                } else {
                    if(name == '') this.warning_user = 1;
                    if(number == '') this.warning_phone =1;
                    if(address == '') this.warning_address = 1;
                }     
                console.log(this.contacts); 
            },
            enter_data: function(event){
                let add_button = document.getElementById("add-btn");
                add_button.click();
                event.target.parentElement.previousSibling.children[0].children[1].focus();
            }
        }
    };

</script>


<style scoped>

footer{
    flex-shrink: 0;
    overflow: hidden;
    border-bottom-left-radius : 10px;
    border-bottom-right-radius: 10px;
}

footer>div {
    padding                   : 8px;
    padding-right             : 4px;
    flex-shrink               : 0;
    background-color          : rgb(245, 245, 245);
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: blue transparent;
}
::-webkit-scrollbar{
    width: 1px;
}

.footer-container{
    padding-right: 12px;
}
.content{
    flex-direction: column;
}
.name{
    flex-basis: 20%;
}
.number{
    margin-top: 2px;
    margin-bottom: 2px;
}
.input-field {
    border : none;
    background: transparent;    
    flex-grow: 1;
    width: 0;
    border-radius: 4px;
    font-size: 14px;
}

.input-field:focus,
.input-field:active,
.input-field:hover {
    border : none;
    outline: none;
}

.icon { 
    padding-left: 8px; 
    width: 30px;
    display: flex;
    color: rgb(150, 150, 150);
} 

.icon::before{
    align-self: center;
}

.warning{
    color: #dc3545;
}

#add-btn {    
    padding      : 6px;
    font-weight  : bold;
    cursor       : pointer;
    color        : rgb(150, 150, 150);
    border-radius: 4px;
}

#add-btn:hover {
    background-color: rgb(230, 230, 230);
}

@media (min-width: 414px){
    footer>div{
        padding: 10px 4px 10px 8px;
    }
    .content{
        flex-direction: row;
        margin-bottom: 8px;
    }
    .name{
        margin-bottom: 0;
    }
    .number{
    padding-left: 12px;
    margin-top: 0;
    margin-bottom: 0;
    }
    .input-field{
        background: rgb(235, 235, 235);    
    }
}
</style>