import ContactItem from "./ContactItem";

class Contacts{

    constructor(){
        this.items = this.loadFromLocalStorage();
    }

    add(item){
        this.items.push(item);
        this.items.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : -1);
        this.saveToLocalStorage();
    }

    delete(index){
        this.items.splice(index, 1);
        this.saveToLocalStorage();
    }


    saveToLocalStorage(){
        localStorage.setItem('contacts-vue', JSON.stringify(this.items));
    }

    loadFromLocalStorage(){
        var json = localStorage.getItem('contacts-vue');

        if(json === null)
            return [];

        json = JSON.parse(json);
        console.log(json);
        let contactItems = [];

        for (let i = 0; i < json.length; i++) {
            contactItems.push(ContactItem.fromJSON(json[i]));
            // contactItems.push(json[i]);  
        }
        
        contactItems = contactItems.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : -1);
        console.log(contactItems);
        return contactItems;
    }
}

export default Contacts;